* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #f5f5f5;
  padding: 20px;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

header {
  color: #fff;
  text-align: center;
  background-color: #004080;
  padding: 20px 0;
}

header h1 {
  margin-bottom: 10px;
}

nav ul {
  justify-content: center;
  gap: 20px;
  list-style: none;
  display: flex;
}

nav ul li a {
  color: #fff;
  font-weight: bold;
  text-decoration: none;
}

nav ul li a.active {
  text-decoration: underline;
}

main {
  background-color: #fff;
  border-radius: 8px;
  max-width: 800px;
  margin: 30px auto;
  padding: 20px;
}

.highlights ul {
  margin-left: 20px;
  list-style-type: disc;
}

footer {
  text-align: center;
  color: #666;
  margin-top: 30px;
  font-size: .9em;
}
